body {
  background-image: -webkit-gradient(linear, left bottom, left top, from(rgba(39, 64, 70, 0.6)), to(rgba(230, 218, 218, 0.7))), url("../images/wp-parchment.jpg");
  background-image: -webkit-linear-gradient(bottom, rgba(39, 64, 70, 0.6), rgba(230, 218, 218, 0.7)), url("../images/wp-parchment.jpg");
  background-image: -o-linear-gradient(bottom, rgba(39, 64, 70, 0.6), rgba(230, 218, 218, 0.7)), url("../images/wp-parchment.jpg");
  background-image: linear-gradient(to top, rgba(39, 64, 70, 0.6), rgba(230, 218, 218, 0.7)), url("../images/wp-parchment.jpg");
  background-size: 100% 100vh, 100% 100vh;
  background-attachment: fixed; }

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center; }

.main__box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
